$ebi: 'OpenSansExtraBoldItalic';
$osr: 'OpenSansRegular';
$rr: 'RobotoRegular';
$rb: 'RobotoBold';
$rm: 'RobotoMedium';



@font-face {
font-family: 'OpenSansExtraBoldItalic';
src: url('../fonts/OpenSansExtraBoldItalic/OpenSansExtraBoldItalic.eot') format('embedded-opentype'),
  url('../fonts/OpenSansExtraBoldItalic/OpenSansExtraBoldItalic.woff2') format('woff2'),
  url('../fonts/OpenSansExtraBoldItalic/OpenSansExtraBoldItalic.woff') format('woff'),
  url('../fonts/OpenSansExtraBoldItalic/OpenSansExtraBoldItalic.ttf') format('truetype'),
  url('../fonts/OpenSansExtraBoldItalic/OpenSansExtraBoldItalic.svg') format('svg');
  font-style: normal;
}

@font-face {
font-family: 'RobotoRegular';
src: url('../fonts/RobotoRegular/RobotoRegular.eot') format('embedded-opentype'),
  url('../fonts/RobotoRegular/RobotoRegular.woff2') format('woff2'),
  url('../fonts/RobotoRegular/RobotoRegular.woff') format('woff'),
  url('../fonts/RobotoRegular/RobotoRegular.ttf') format('truetype'),
  url('../fonts/RobotoRegular/RobotoRegular.svg') format('svg');
  font-style: normal;
  font-weight: normal;
}
